<script setup lang="ts">
const route = useRoute();
const router = useRouter();
const storeUrl = route.query.storeUrl as string;

onMounted(() => {
  if (!storeUrl || storeUrl === "undefined")
    return router.push({ path: "/" });

  window.location.href = storeUrl;
});
</script>

<template>
  <div> Redirecting ...</div>
</template>
